import React, { Component } from 'react';
import metaStellar from '../ethereum/metaStellar.js';
import {Layout, Astro, Constellation} from '../components/index';
import sampleAstros from '../constellation.json';
import VirtualSky from '../components/VirtualSky';

class MetaStellarIndex extends Component {
  static async getInitialProps() {
    const lastId = await metaStellar.methods.lastId().call();
    const starIndexes = Array.from(Array(lastId)).map((e,i)=>i+1);

    const astros = Promise.all(starIndexes.map(async id => {
      const rawAstro = await metaStellar.methods.getAstro(id).call();
      return {
        target: {name: rawAstro.name},
        ra: {decimal: rawAstro.raDecimal},
        dec: {decimal: rawAstro.decDecimal}
      }
    }));

    return { astros };
  }

  constructor() {
    super();
    this.state = {
      astros: [],
      mapShow: true,
      loading: false
    }
  }

  componentDidMount() {
    const starIndexes = Array.from(Array(this.props.lastId)).map((e,i)=>i+1);
    this.renderAstros(starIndexes)
        .then(() => this.setState({ loading: false }));
  }

  testClick() {
    this.setState({
      mapShow: !this.state.mapShow
    })
  }

  render() {
    return(
        <Layout>
          <VirtualSky astros={this.state.astros} mapShow={this.state.mapShow} />
        </Layout>
    )
  }
}

export default MetaStellarIndex;



